@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}body{background-color:#181818;font-family:"Dosis",sans-serif;color:#f6f6f6;padding-bottom:10vh}#pomodoro-container{width:50%;margin:auto;display:flex;flex-flow:row wrap;justify-content:center}#pomodoro-container #title-header{text-align:center;width:100%}#pomodoro-container .timer-box{width:250px;background-color:#262626;padding:2% 3%;margin:1%;border-radius:15px;border:1px solid rgba(246,246,246,.1)}#pomodoro-container .timer-box h4{font-weight:500;margin:0}#pomodoro-container .timer-box hr{opacity:.1}#pomodoro-container .timer-box h1,#pomodoro-container .timer-box p{text-align:center}#pomodoro-container .timer-box h1{margin:3%}#pomodoro-container .timer-box p{margin-top:-2%}#pomodoro-container .timer-box .timer-controller{display:flex;flex-flow:row nowrap;align-items:baseline;justify-content:center}#pomodoro-container .timer-box .timer-controller .display-time{display:inline;width:50%}#pomodoro-container .timer-box button{background-color:#262626;color:rgba(246,246,246,.8);border:1px solid rgba(246,246,246,.5);padding:3% 4%;border-radius:8px;margin:0 1%}#pomodoro-container .timer-box button:hover{cursor:pointer;border:1px solid #f6f6f6;background-color:#181818;transition:background-color .8s ease .2s,border .8s ease .2s}#pomodoro-container .timer-box button:active{opacity:.5}#pomodoro-container #session-box{width:520px}#pomodoro-container #session-box #timer-label{font-weight:600;text-align:center;margin-bottom:0}#pomodoro-container #session-box h1{font-size:5rem}#pomodoro-container #session-box .time-left-controller{text-align:center;margin-bottom:5%}#pomodoro-container #session-box .time-left-controller button{width:30%;font-size:1.25rem}@media screen and (max-width: 1070px){#pomodoro-container{width:100%}}@media screen and (max-width: 350px){body{min-width:350px;overflow:scroll}}
